import Logo from "@/public/logo/Logo.png";
import Image from "next/image";

export default function Header() {
  return (
    <header>
      <div className="sticky w-full flex-box background-white z-10">
        <div className="logo-wrapper">
          <Image
            src={Logo.src}
            alt="Logo"
            className="logo-image"
            width={150}
            height={43}
          />
        </div>
      </div>
    </header>
  );
}
